/* App.css */

body {
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
}

.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

h1 {
  color: #333;
  margin-bottom: 20px;
}

hr {
  border: none;
  border-top: 1px solid #ddd;
  margin: 20px 0;
}

button {
  padding: 10px 15px;
  border: none;
  color: #2e2121;
  cursor: pointer;
  border-radius: 5px;
  font-size: 14px;
  transition: 0.3s ease;
}

button.primary {
  background-color: #007bff; /* Blue color for primary buttons */
}

.DeleteButton {
  background-color: #dc3545;
}

button.add-device {
  background-color: #28a745; /* Green color for add-device buttons */
}

/* Room button style */

.RoomButton {
  width: 100%;
}

/* ShowRoom component */

.RoomCard {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 1;
}

/* Room component */
.Room {
  border: 1px solid #ddd;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  background-color: #f9f9f9;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

/* Room Actions */
.RoomActions {
  margin-top: 20px;
}

.ActionButton {
  padding: 10px 15px;
  margin: 10px;
  border: none;
  color: #0c0c0c;
  cursor: pointer;
  border-radius: 5px;
  font-size: 14px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.ActionButton {
  background-color: #007bff;
}

.ActionButton.danger {
  background-color: #dc3545;
}

.ActionButton.add-device {
  background-color: #28a745;
}

/* Device buttons */
.DeviceButton {
  margin-right: 10px;
  padding: 10px 15px;
  border: none;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  font-size: 14px;
}

/* AddNewRoom component */

.AddNewRoom {
  border: 1px solid #ddd;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  background-color: #e9ecef; /* Light Gray */
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Devices component */

.Devices {
  border: 1px solid #ddd;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  background-color: #f8f9fa; /* Gray */
}

.Device-button {
  margin-right: 10px;
}

/* Improve layout for ShowRoom component */

.ShowRoom {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  justify-items: center;
  align-items: center;
}

/* Styling for select element */
select {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f2f2f2; /* Background color for select */
  color: #333; /* Text color for select */
  font-size: 14px;
}

/* Styling for option elements */
option {
  background-color: #f2f2f2; /* Background color for options */
  color: #333; /* Text color for options */
  font-size: 14px;
}
